import React, { useEffect, useState } from "react";
import Header from "../../components/header";
import { toast } from "react-toastify";
import { defaults } from "chart.js";
import {
  Chart as ChartJS,
  RadialLinearScale,
  PointElement,
  LineElement,
  Filler,
  Tooltip,
  Legend,
} from "chart.js";
import { Radar } from "react-chartjs-2";
import axios from "axios";
import { Link, useNavigate } from "react-router-dom";

ChartJS.register(
  RadialLinearScale,
  PointElement,
  LineElement,
  Filler,
  Tooltip,
  Legend
);

const CalculatorResult = () => {
  defaults.font.family = "carbonFontBold";

  const navigate = useNavigate();

  const [dataRes, setDataRes] = useState({});
  const [data, setData] = useState({
    labels: ["", "", "   ", " ", ""],
    datasets: [
      {
        label: "  ",
        data: [0, 0, 0, 0, 0],
        backgroundColor: "#50d5b659",
        borderColor: "#067d68",
        borderWidth: 1,
        width: "200px",
      },
    ],
  });
  const [dataResultAverage, setDataResultAverage] = useState({
    person: {
      percent: 30,
      color: "red",
    },
    iran: {
      percent: 30,
      color: "yellow",
    },
    world: {
      percent: 30,
      color: "green",
    },
  });

  const session_id = JSON.parse(localStorage.getItem("session_id"));

  useEffect(() => {
    axios
      .post("http://146.70.87.250:8001/calculator/results", {
        session_id: parseInt(session_id),
      })
      .then((res) => {
        setDataRes(res.data);
        setData({
          labels: [
            "",
            "",
            "   ",
            " ",
            "",
          ],
          datasets: [
            {
              label: "  ",
              data: [
                res.data.carbon.carbon_tone_results.house_tone_carbon,
                res.data.carbon.carbon_tone_results.car_tone_carbon,
                res.data.carbon.carbon_tone_results.publict_tone_carbon,
                res.data.carbon.carbon_tone_results.motor_tone_carbon,
                0,
              ],
              backgroundColor: "#50d5b659",
              borderColor: "#067d68",
              borderWidth: 1,
              width: "200px",
            },
          ],
        });
        setDataResultAverage(res.data.averages);
      })
      .catch((e) => {
        toast.error("    ");
      });
  }, []);

  const handelerOffset = () => {
    // navigate("/calculatorPayment", { state: dataRes });
    navigate("/calculatorPayment", { state: { dataRes } });
  };

  const Progress = ({ done, title ,color }) => {
    const [style, setStyle] = React.useState({});

    setTimeout(() => {
      const newStyle = {
        opacity: 1,
        width: `${done}%`,
      };

      setStyle(newStyle);
    }, 200);

    return (
      <div className="progress-container">
        <p className="title-progress-lineBar">{title}</p>
        <div className="progress-lineBar">
          <div className={`progress-done-lineBar progress-lineBar-${color}`} style={style}>
            {/* {done}% */}
          </div>
        </div>
      </div>
    );
  };

  return (
    <div className="main-calculator">
      <div className="header-container-pages">
        <Header />
      </div>
      <section className="calculator">
        <div className="calculator-container">
          <div className="box-menu-calculator">
            <div className="menu-calculator">
              <Link to={"/calculator"}>
                <button className="item-menu-calculator">
                  <i class="fa-regular fa-calendar-days"></i>
                  <span> </span>
                </button>
              </Link>
            </div>
            <div className="menu-calculator">
              <Link to={"/calculatorHome"}>
                <button className="item-menu-calculator">
                  <i class="fa-solid fa-house-user"></i>
                  <span></span>
                </button>
              </Link>
            </div>
            <div className="menu-calculator">
              <Link to={"/calculatorCar"}>
                <button className="item-menu-calculator">
                  <i class="fa-solid fa-car"></i>
                  <span></span>
                </button>
              </Link>
            </div>
            <div className="menu-calculator">
              <Link to={"/calculatorPublicTransport"}>
                <button className="item-menu-calculator">
                  <i class="fa-solid fa-bus"></i>
                  <span>   </span>
                </button>
              </Link>
            </div>
            <div className="menu-calculator">
              <Link to={"/calculatorMotorBike"}>
                <button className="item-menu-calculator">
                  <i class="fa-solid fa-motorcycle"></i>
                  <span> </span>
                </button>
              </Link>
            </div>
            <div className="menu-calculator">
              <button className="item-menu-calculator">
                <i class="fa-sharp fa-solid fa-plane"></i>
                <span></span>
              </button>
            </div>
            <div className="menu-calculator">
              <Link to={"/calculatorResult"}>
                <button className="item-menu-calculator active-btn">
                  <i class="fa-solid fa-square-poll-vertical"></i>
                  <span></span>
                </button>
              </Link>
            </div>
          </div>
          <div className="container-content-calculator">
            <div className="box-title-content-calculator">
              <p></p>
            </div>
            <div className="contetn-calculator">
              <Radar
                data={data}
                height={500}
                options={{ maintainAspectRatio: false }}
              />
            </div>

            <section className="progress-line">
              <Progress done="20" title=" :" color={dataResultAverage.iran.color}/>
              <Progress done="50" title=" :" color={dataResultAverage.person.color} />
              <Progress done="30" title=" :" color={dataResultAverage.world.color}/>
            </section>

            <section className="container-progress-bar">
              <article>
                {/* <input
                  type="radio"
                  name="exercises"
                  id="exercise-1"
                  hidden
                  checked
                /> */}
                {/* <input type="radio" name="exercises" id="exercise-2"/>
                <input type="radio" name="exercises" id="exercise-3"/>
                <input type="radio" name="exercises" id="exercise-4"/> */}

                {/* sassss */}
                {/* <div class="chart grid">
                  <div class="exercise second">
                    <div class={`bar ${dataResultAverage.iran.color}  bar-${dataResultAverage.iran.percent}   lightGray-face`}>
                      <div class="face top">
                        <div class="growing-bar"></div>
                      </div>
                      <div class="face side-0">
                        <div class="growing-bar"></div>
                      </div>
                      <div class="face floor">
                        <div class="growing-bar"></div>
                      </div>
                      <div class="face side-a"></div>
                      <div class="face side-b"></div>
                      <div class="face side-1">
                        <div class="growing-bar"></div>
                      </div>
                    </div>
                  </div>
                  <div class="exercise second">
                    <div class={`bar ${dataResultAverage.person.color}  bar-${dataResultAverage.person.percent} lightGray-face`}>
                      <div class="face top">
                        <div class="growing-bar"></div>
                      </div>
                      <div class="face side-0">
                        <div class="growing-bar"></div>
                      </div>
                      <div class="face floor">
                        <div class="growing-bar"></div>
                      </div>
                      <div class="face side-a"></div>
                      <div class="face side-b"></div>
                      <div class="face side-1">
                        <div class="growing-bar"></div>
                      </div>
                    </div>
                  </div>
                  <div class="exercise second">
                    <div class={`bar ${dataResultAverage.world.color}  bar-${dataResultAverage.world.percent} lightGray-face`}>
                      <div class="face top">
                        <div class="growing-bar"></div>
                      </div>
                      <div class="face side-0">
                        <div class="growing-bar"></div>
                      </div>
                      <div class="face floor">
                        <div class="growing-bar"></div>
                      </div>
                      <div class="face side-a"></div>
                      <div class="face side-b"></div>
                      <div class="face side-1">
                        <div class="growing-bar"></div>
                      </div>
                    </div>
                  </div> */}
                {/* <div class="exercise second">
                    <div class={`bar ${dataResultAverage.iran.color}  bar-${dataResultAverage.iran.percent}   lightGray-face`} >
                      <div class="face top">
                        <div class="growing-bar"></div>
                      </div>
                      <div class="face side-0">
                        <div class="growing-bar"></div>
                      </div>
                      <div class="face floor">
                        <div class="growing-bar"></div>
                      </div>
                      <div class="face side-a"></div>
                      <div class="face side-b"></div>
                      <div class="face side-1">
                        <div class="growing-bar"></div>
                      </div>
                    </div>
                    <div class={`bar ${dataResultAverage.person.color}  bar-${dataResultAverage.person.percent} lightGray-face`}>
                      <div class="face top">
                        <div class="growing-bar"></div>
                      </div>
                      <div class="face side-0">
                        <div class="growing-bar"></div>
                      </div>
                      <div class="face floor">
                        <div class="growing-bar"></div>
                      </div>
                      <div class="face side-a"></div>
                      <div class="face side-b"></div>
                      <div class="face side-1">
                        <div class="growing-bar"></div>
                      </div>
                    </div>
                    <div class={`bar ${dataResultAverage.world.color}  bar-${dataResultAverage.world.percent} lightGray-face`}>
                      <div class="face top">
                        <div class="growing-bar"></div>
                      </div>
                      <div class="face side-0">
                        <div class="growing-bar"></div>
                      </div>
                      <div class="face floor">
                        <div class="growing-bar"></div>
                      </div>
                      <div class="face side-a"></div>
                      <div class="face side-b"></div>
                      <div class="face side-1">
                        <div class="growing-bar"></div>
                      </div>
                    </div>
                  </div> */}
                {/* </div> */}

                {/* <p><em>It's liquid</em>, so adding a bit of code you can make charts like this :P</p>
                <nav class="actions">
                    <label for="exercise-1">E-1</label>
                    <label for="exercise-2">E-2</label>
                    <label for="exercise-3">E-3</label>
                    <label for="exercise-4">E-4</label>
                </nav> */}
              </article>
            </section>
            <button className="offset-btn" onClick={handelerOffset}>
              
            </button>
          </div>
        </div>
      </section>

      <footer>
        <p>           .</p>
      </footer>
    </div>
  );
};

export default CalculatorResult;
