import React, { useEffect, useState } from "react";
import { useLocation } from "react-router-dom";
import Header from "../../components/header";

const CalculatorPayment = () => {
  const { state } = useLocation();
  const { dataRes } = state;
  console.log("====================================");
  console.log(dataRes);
  console.log("====================================");
  const [stroke, setStroke] = useState(375);
  // const [data, setData] = useState({
  //   house: 54545,
  //   car: 754540,
  //   publict: 25653,
  //   motor: 244343,
  //   air: 5555,
  //   result: 1084636,
  // });
  const [dataResult, setDataResult] = useState({
    house: dataRes.price.house_price,
    car: dataRes.price.car_price,
    publict: dataRes.price.publict_price,
    motor: dataRes.price.motor_price,
    air: 0,
  });

  // setTimeout(() => setStroke(800), 0);

  const [dataItem, setDataItem] = useState({
    house: true,
    car: true,
    publict: true,
    motor: true,
    air: true,
  });

  useEffect(() => {
    charechter();
  }, [dataResult]);

  function separate(Number) {
    Number += "";
    Number = Number.replace(",", "");
    let x = Number.split(".");
    let y = x[0];
    let z = x.length > 1 ? "." + x[1] : "";
    var rgx = /(\d+)(\d{3})/;
    while (rgx.test(y)) y = y.replace(rgx, "$1" + "," + "$2");
    return y + z;
  }

  // const [result, setResult] = useState(dataResult.result);
  const setDataProperty = (key, e) => {
    // eslint-disable-next-line default-case
    switch (key) {
      case "air":
        if (dataItem.air) {
          setDataItem({ ...dataItem, air: false });
          setDataResult({ ...dataResult, air: 0 });
        } else {
          setDataItem({ ...dataItem, air: true });
          setDataResult({ ...dataResult, air: 0 });
        }
        break;
      case "house":
        if (dataItem.house) {
          setDataItem({ ...dataItem, house: false });
          setDataResult({ ...dataResult, house: 0 });
        } else {
          setDataItem({ ...dataItem, house: true });
          setDataResult({ ...dataResult, house: dataRes.price.house_price });
        }
        break;
      case "car":
        if (dataItem.car) {
          setDataItem({ ...dataItem, car: false });
          setDataResult({ ...dataResult, car: 0 });
        } else {
          setDataItem({ ...dataItem, car: true });
          setDataResult({ ...dataResult, car: dataRes.price.car_price });
        }
        break;
      case "publict":
        if (dataItem.publict) {
          setDataItem({ ...dataItem, publict: false });
          setDataResult({ ...dataResult, publict: 0 });
        } else {
          setDataItem({ ...dataItem, publict: true });
          setDataResult({
            ...dataResult,
            publict: dataRes.price.publict_price,
          });
        }

        break;
      case "motor":
        if (dataItem.motor) {
          setDataItem({ ...dataItem, motor: false });
          setDataResult({ ...dataResult, motor: 0 });
        } else {
          setDataItem({ ...dataItem, motor: true });
          setDataResult({ ...dataResult, motor: dataRes.price.motor_price });
        }
        break;
    }

    // charechter()
  };

  const charechter = () => {
    let result =
      dataResult.house +
      dataResult.car +
      dataResult.publict +
      dataResult.motor +
      dataResult.air;

    let precentResult = (result * 100) / dataRes.price.final_general_price;

    let precentResultAsli =precentResult - 100
// console.log('====================================');
// console.log(result);
// console.log('====================================');
// console.log('====================================');
// console.log(dataRes.price.final_general_price);
// console.log('====================================');
// console.log('====================================');
// console.log(precentResult - 100);
// console.log('====================================');


    let strokeDashoffset = Math.abs(precentResultAsli * 6.25);

    setStroke(strokeDashoffset+375);
    // console.log("====================================");
    // console.log(strokeDashoffset);
    // console.log("====================================");
  };
  //  charechter()
  let styleBgChoise = {
    backgroundColor: "white",
  };
  let styleTextChoise = {
    color: "white",
  };

  // console.log("====================================");
  // console.log(result);
  // console.log("====================================");

  return (
    <div className="main-calculator">
      <div className="header-container-pages">
        <Header />
      </div>
      <section className="calculator-payment">
        <div className="calculator-payment-container">
          <h1> </h1>

          <div className="choise-calculator-offset">
            <div className="box-input-choise-calculator-offset">
              <label
                class="container-label"
                style={dataItem.house ? styleBgChoise : null}
              >
                <i class="fa-solid fa-house-user"></i>
                <p></p>
                <input
                  type="checkbox"
                  defaultChecked="checked"
                  onChange={(e) => {
                    setDataProperty("house", e);
                  }}
                />
                <span class="checkmark"></span>
              </label>
              <div
                className="price-calculator-offset-item"
                style={dataItem.house ? styleTextChoise : null}
              >
                <p>{separate(dataRes.price.house_price)}</p>
                <p></p>
              </div>
            </div>
            <div className="box-input-choise-calculator-offset">
              <label
                class="container-label"
                style={dataItem.car ? styleBgChoise : null}
              >
                <i class="fa-solid fa-car"></i>
                <p></p>
                <input
                  type="checkbox"
                  defaultChecked="checked"
                  onChange={(e) => {
                    setDataProperty("car", e);
                  }}
                />
                <span class="checkmark"></span>
              </label>
              <div
                className="price-calculator-offset-item"
                style={dataItem.car ? styleTextChoise : null}
              >
                <p>{separate(dataRes.price.car_price)}</p>
                <p></p>
              </div>
            </div>
            <div className="box-input-choise-calculator-offset">
              <label
                class="container-label"
                style={dataItem.publict ? styleBgChoise : null}
              >
                <i class="fa-solid fa-bus"></i>
                <p>   </p>
                <input
                  type="checkbox"
                  defaultChecked="checked"
                  onChange={(e) => {
                    setDataProperty("publict", e);
                  }}
                />
                <span class="checkmark"></span>
              </label>
              <div
                className="price-calculator-offset-item"
                style={dataItem.publict ? styleTextChoise : null}
              >
                <p>{separate(dataRes.price.publict_price)}</p>
                <p></p>
              </div>
            </div>
            <div className="box-input-choise-calculator-offset">
              <label
                class="container-label"
                style={dataItem.motor ? styleBgChoise : null}
              >
                <i class="fa-solid fa-motorcycle"></i>
                <p> </p>
                <input
                  type="checkbox"
                  defaultChecked="checked"
                  onChange={(e) => {
                    setDataProperty("motor", e);
                  }}
                />
                <span class="checkmark"></span>
              </label>
              <div
                className="price-calculator-offset-item"
                style={dataItem.motor ? styleTextChoise : null}
              >
                <p>{separate(dataRes.price.motor_price)}</p>
                <p></p>
              </div>
            </div>
            <div className="box-input-choise-calculator-offset">
              <label
                class="container-label"
                style={dataItem.air ? styleBgChoise : null}
              >
                <i class="fa-sharp fa-solid fa-plane"></i>
                <p></p>
                <input
                  type="checkbox"
                  defaultChecked="checked"
                  onChange={(e) => {
                    setDataProperty("air", e);
                  }}
                />
                <span class="checkmark"></span>
              </label>
              <div
                className="price-calculator-offset-item"
                style={dataItem.air ? styleTextChoise : null}
              >
                <p>0</p>
                <p></p>
              </div>
            </div>
          </div>

          <div className="progress">
            <svg id="progress">
              <circle
                id="circle"
                cx={120}
                cy={120}
                r={100}
                strokeDasharray={1000}
                strokeDashoffset={stroke}
                // stroke-dashoffset={}
                transform="rotate(-90) scale(1, -1)"
              />
            </svg>
            <svg id="circleRemaining">
              <circle cx={120} cy={120} r={100} />
            </svg>
            <div className="emoji happy">
              <div className="face">
                <div className="eye left" />
                <div className="eye right" />
                <div className="mouth" />
              </div>
            </div>
          </div>

          <div className="price-all-calculator-offset">
            <h1>
              {console.log(
                dataResult.house +
                  "------ " +
                  dataResult.car +
                  "------ " +
                  dataResult.publict +
                  "------ " +
                  dataResult.motor +
                  "------ " +
                  dataResult.air
              )}
              {separate(dataResult.house +
                dataResult.car +
                dataResult.publict +
                dataResult.motor +
                dataResult.air)}
            </h1>
            <p></p>
          </div>

          <button className="payment-offset-btn">
            
          </button>
        </div>
      </section>
      <footer>
        <p>
                   .
        </p>
      </footer>
    </div>
  );
};

export default CalculatorPayment;
