import { BrowserRouter, Route, Routes } from "react-router-dom";
import { ToastContainer } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import Calculator from "./page/calculator/calculator";
import CalculatorCar from "./page/calculator/calculatorCar";
import CalculatorHome from "./page/calculator/calculatorHome";
import CalculatorMotorBike from "./page/calculator/calculatorMotorBike";
import CalculatorPayment from "./page/calculator/calculatorPayment";
import CalculatorPublicTransport from "./page/calculator/calculatorPublicTransport";
import CalculatorResult from "./page/calculator/calculatorResult";
import Home from "./page/home";
import ClimateChange from "./page/information/climateChange";
import Deforestation from "./page/information/deforestation";
import DevelopmentGoals from "./page/information/developmentGoals";
import Reduce from "./page/information/reduce";
import Page404 from "./page/page404";

function App() {
  return (
    <main>
    <ToastContainer />
      <BrowserRouter>
        <Routes>
          <Route path='*' element={<Page404 />} />
          <Route path="/carbontest/" element={<Home />} />
          <Route path="/calculator" element={<Calculator/>} />
          <Route path="/carbontest/calculatorHome" element={<CalculatorHome/>} />
          <Route path="/carbontest/calculatorCar" element={<CalculatorCar/>} />
          <Route path="/carbontest/calculatorPublicTransport" element={<CalculatorPublicTransport/>} />
          <Route path="/carbontest/calculatorMotorBike" element={<CalculatorMotorBike/>} />
          <Route path="/carbontest/calculatorResult" element={<CalculatorResult/>} />
          <Route path="/carbontest/calculatorPayment" element={<CalculatorPayment/>} />
          <Route path="/carbontest/ClimateChange" element={<ClimateChange/>} />
          <Route path="/carbontest/Deforestation" element={<Deforestation/>} />
          <Route path="/carbontest/DevelopmentGoals" element={<DevelopmentGoals/>} />
          <Route path="/carbontest/Reduce" element={<Reduce/>} />
        </Routes>
      </BrowserRouter>
    </main>
  );
}

export default App;
