import React from "react";
import { Link } from "react-router-dom";
import Header from "../../components/header";
import untargets2 from "../../asset/image/untargets2.png";

const DevelopmentGoals = () => {
  return (
    <div className="main-information">
      <div className="header-container-pages">
        <Header />
      </div>
      <section className="information">
        <h1 className="title-information">UNITED NATIONS SUSTAINABLE DEVELOPMENT GOALS</h1>
        <h3 className="title-p-information">
        At the United Nations (UN) Sustainable Development Summit in 2015, world leaders adopted the 17 Sustainable Development Goals (SDGs). These global goals aim to address inequalities and challenges all across the planet whilst aiding development and protecting the environment. Meeting the target date of 2030 will require the work and co-operation of all these governments and the UN along with businesses and members of the public.
        </h3>
        <img src={untargets2}  width="500px" style={{ alignSelf: "center"}} alt="img" />
        <h3 className="title-p-information">
        The Sustainable Development Goals are:        </h3>
        <hr style={{width: "100%"}}/>
        <p>
        Goal 2: Eradicate hunger by ensuring universal access to safe, nutritious food and ensuring sustainable food production that is resilient to climate change and extreme events</p>
        {/* <hr style={{width: "100%"}}/> */}
        <hr style={{width: "100%"}}/>
        <p>
        Goal 3: Guarantee the health and wellbeing of all by reducing deaths, combating epidemics and ensuring universal health-care coverage including access to sexual and reproductive health-care services and education        </p>
        {/* <hr style={{width: "100%"}}/> */}
        <hr style={{width: "100%"}}/>
        <p>
        Goal 4: Ensure free, equitable and quality primary and secondary education to all and equal access to affordable and quality technical and vocational education such as university    </p>
        {/* <hr style={{width: "100%"}}/> */}
        <hr style={{width: "100%"}}/>
        <p>
        Goal 5: Achieve gender equality; ending discrimination, violence and harmful practices against women and girls, ensuring women have equal access and rights to economic resources (such as land ownership) and full participation and equal opportunities for leadership at all levels of decision-making

</p>
        <hr style={{width: "100%"}}/>
      
      
      </section>
      <footer>
        <p>           .</p>
      </footer>
    </div>
  );
};

export default DevelopmentGoals;
