import React, { useEffect, useState } from "react";
import Header from "../../components/header";
import axios from "axios";
import { Link, useNavigate } from "react-router-dom";
import Loading from "../../components/loading";
import { toast } from 'react-toastify';

const CalculatorMotorBike = () => {
  const [loading, setLoading] = useState(false);

  const session_id = JSON.parse(localStorage.getItem("session_id"));
  const mileageMotorBike = JSON.parse(localStorage.getItem("mileageMotorBike"));
  const efficiencyMotorBike = JSON.parse(
    localStorage.getItem("efficiencyMotorBike")
  );

  let navigate = useNavigate();
  const [textRes, setTextRes] = useState();
  const [dataResult, setDataResult] = useState({});

  useEffect(() => {
    setDataResult({
      efficiency: efficiencyMotorBike,
      mileage: mileageMotorBike,
    });
  }, []);

  const onSubmit = (e, validNavigate) => {
    e.preventDefault();

    setLoading(true);

    let mileageResult = dataResult.mileage;
    let efficiencyResult = dataResult.efficiency;

    if (mileageResult === undefined) {
      mileageResult = mileageMotorBike;
    }

    if (efficiencyResult === undefined) {
      efficiencyResult = efficiencyMotorBike;
    }

    axios
      .post("http://146.70.87.250:8001/calculator/motorbike", {
        session_id: parseInt(session_id),
        mileage: parseInt(mileageResult),
        efficiency: parseInt(efficiencyResult),
      })
      .then((res) => {
        if (validNavigate) {
          navigate("/calculatorResult");
        } else {
          
          setTextRes(
            <>
              <i class={`fas fa-cloud color-smoke-${res.data.color}`}></i>
              <p>  :
              &nbsp;
              <span className={`font-english color-smoke-${res.data.color}`}>
                 {res.data.carbon_metric_tone} metric tons of CO2e 
              </span>
              
              </p>

              <div id="smoke">
                <span class="s0">
                  <i class={`fas fa-cloud color-smoke-${res.data.color}`}></i>
                </span>
                <span class="s1">
                  <i class={`fas fa-cloud color-smoke-${res.data.color}`}></i>
                </span>
                <span class="s2">
                  <i class={`fas fa-cloud color-smoke-${res.data.color}`}></i>
                </span>
                <span class="s3">
                  <i class={`fas fa-cloud color-smoke-${res.data.color}`}></i>
                </span>
                <span class="s4">
                  <i class={`fas fa-cloud color-smoke-${res.data.color}`}></i>
                </span>
                <span class="s5">
                  <i class={`fas fa-cloud color-smoke-${res.data.color}`}></i>
                </span>
                <span class="s6">
                  <i class={`fas fa-cloud color-smoke-${res.data.color}`}></i>
                </span>
                <span class="s7">
                  <i class={`fas fa-cloud color-smoke-${res.data.color}`}></i>
                </span>
                <span class="s8">
                  <i class={`fas fa-cloud color-smoke-${res.data.color}`}></i>
                </span>
                <span class="s9">
                  <i class={`fas fa-cloud color-smoke-${res.data.color}`}></i>
                </span>
              </div>
            </>
          );
        }

        localStorage.setItem("mileageMotorBike", JSON.stringify(mileageResult));
        localStorage.setItem(
          "efficiencyMotorBike",
          JSON.stringify(efficiencyResult)
        );

        setLoading(false);
      })
      .catch((e) => {
        toast.error("    ");
        setLoading(false)
      });
  };
  const setDataProperty = (key, e) => {
    console.log("dddddd");
    switch (key) {
      case "mileage":
        console.log(e.target.value);
        setDataResult({ ...dataResult, mileage: e.target.value });
        break;
      case "efficiency":
        setDataResult({ ...dataResult, efficiency: e.target.value });
        break;
    }
  };

  return (
    <>
      {loading ? <Loading /> : null}
      <div className="main-calculator">
        <div className="header-container-pages">
          <Header />
        </div>
        <section className="calculator">
          <div className="calculator-title-top">
            <h1>  </h1>
            <p>        </p>
          </div>
          <div className="calculator-container">
            <div className="box-menu-calculator">
              <div className="menu-calculator">
                <Link to={"/calculator"}>
                  <button className="item-menu-calculator">
                    <i class="fa-regular fa-calendar-days"></i>
                    <span> </span>
                  </button>
                </Link>
              </div>
              <div className="menu-calculator">
                <Link to={"/calculatorHome"}>
                  <button className="item-menu-calculator">
                    <i class="fa-solid fa-house-user"></i>
                    <span></span>
                  </button>
                </Link>
              </div>
              <div className="menu-calculator">
                <Link to={"/calculatorCar"}>
                  <button className="item-menu-calculator">
                    <i class="fa-solid fa-car"></i>
                    <span></span>
                  </button>
                </Link>
              </div>
              <div className="menu-calculator">
                <Link to={"/calculatorPublicTransport"}>
                  <button className="item-menu-calculator ">
                    <i class="fa-solid fa-bus"></i>
                    <span>   </span>
                  </button>
                </Link>
              </div>
              <div className="menu-calculator">
                <Link to={"/calculatorMotorBike"}>
                  <button className="item-menu-calculator active-btn">
                    <i class="fa-solid fa-motorcycle"></i>
                    <span> </span>
                  </button>
                </Link>
              </div>
              <div className="menu-calculator">
                <button className="item-menu-calculator">
                  <i class="fa-sharp fa-solid fa-plane"></i>
                  <span></span>
                </button>
              </div>
              <div className="menu-calculator">
                <Link to={"/calculatorResult"}>
                  <button className="item-menu-calculator">
                    <i class="fa-solid fa-square-poll-vertical"></i>
                    <span></span>
                  </button>
                </Link>
              </div>
            </div>
            <div className="container-content-calculator">
              <div className="box-title-content-calculator">
                <p>    </p>
              </div>
              <div className="contetn-calculator">
                <div className="align-item-calculator">
                  <div className="box-row-input-calculator">
                    <div className="row-form-input-calculator">
                      <label htmlFor="distance-input">  : </label>
                      <div style={{ direction: "rtl" }}>
                        <input
                          type="number"
                          name="distance"
                          placeholder="km"
                          id="distance-input"
                          value={dataResult.mileage}
                          onChange={(e) => {
                            setDataProperty("mileage", e);
                          }}
                        />
                      </div>
                    </div>
                  </div>
                  <div className="box-row-input-calculator">
                    <div className="row-form-input-calculator">
                      <label htmlFor="fuel-input">
                          (  100 KM) :{" "}
                      </label>
                      <div style={{ direction: "rtl" }}>
                        <input
                          type="number"
                          name="fuel"
                          placeholder="liter"
                          id="fuel-input"
                          value={dataResult.efficiency}
                          onChange={(e) => {
                            setDataProperty("efficiency", e);
                          }}
                        />
                      </div>
                    </div>
                  </div>

                  <button
                    className="btn-calculator-calculation"
                    onClick={(e) => {
                      onSubmit(e, false);
                    }}
                  >
                    
                  </button>
                  <div className="box-result-calculator">{textRes}</div>
                </div>
              </div>
            </div>
            <button
              className="btn-calculator-next"
              onClick={(e) => {
                onSubmit(e, true);
              }}
            >
              
            </button>
          </div>
        </section>
        <footer>
          <p>           .</p>
        </footer>
      </div>
    </>
  );
};

export default CalculatorMotorBike;
