import React, { useEffect, useState } from "react";
import DatePicker from "react-multi-date-picker";
import persian_fa from "react-date-object/locales/persian_fa";
import persian from "react-date-object/calendars/persian";
import Header from "../../components/header";
import axios from "axios";
import { Link, useNavigate } from "react-router-dom";
import Loading from "../../components/loading";
import { toast } from "react-toastify";
import Select from "react-select";

const optionsState = [];
const optionsCity = [];
const Calculator = () => {
  let navigate = useNavigate();
  const [dataResult, setDataResult] = useState({});
  const [selectedStateOption, setSelectedStateOption] = useState(null);
  const [selectedCityOption, setSelectedCityeOption] = useState(null);
  const [state_id, setState_id] = useState("");
  const [city_id, setCity_id] = useState("");


  const [loading, setLoading] = useState(false);

  useEffect(() => {
    setLoading(true);
    axios
      .get("http://146.70.87.250:8001/calculator/get_provinces/")
      .then((res) => {
        res.data.map((item) => {
          optionsState.push({ value: item.state_id, label: item.state_name });
        });
        setLoading(false);
      })
      .catch((e) => {
        toast.error("    ");
        setLoading(false);
      });
  }, []);


  const onSubmit = (e) => {
    e.preventDefault();

    setLoading(true);

    localStorage.setItem("members", null);
    localStorage.setItem("electricity", null);
    localStorage.setItem("gas", null);
    localStorage.setItem("propane", null);
    localStorage.setItem("coal", null);
    localStorage.setItem("oil", null);
    localStorage.setItem("lpg", null);
    localStorage.setItem("mileage", null);
    localStorage.setItem("efficiency", null);
    localStorage.setItem("mileageMotorBike", null);
    localStorage.setItem("efficiencyMotorBike", null);
    localStorage.setItem("bus", null);
    localStorage.setItem("train", null);
    localStorage.setItem("metro", null);
    localStorage.setItem("taxi", null);

    axios
      .post("http://146.70.87.250:8001/calculator/create_session", {
        end_date: dataResult.end_date,
        start_date: dataResult.start_date,
        location_state: state_id,
        location_city: city_id,
      })
      .then((res) => {
        navigate("/calculatorHome");
        localStorage.setItem("session_id", JSON.stringify(res.data.session_id));
        setLoading(false);
      })
      .catch((e) => {
        toast.error("    ");
        setLoading(false);
      });
  };
  const setDataProperty = (key, e) => {
    switch (key) {
      case "start_date":
        setDataResult({ ...dataResult, start_date: e });
        break;
      case "end_date":
        setDataResult({ ...dataResult, end_date: e });
        break;
    }
  };


  const handelerOptionCity = (e) => {
    setState_id(e.value)

    setLoading(true);

    axios
    .get("http://146.70.87.250:8001/calculator/get_cities/"+e.value)
    .then((res) => {
      res.data.map((item) => {
        optionsCity.push({ value: item.city_id, label: item.city_name });
      });
      setLoading(false);
    })
    .catch((e) => {
      toast.error("    ");
      setLoading(false);
    });
  };

  const handelerOptionCityCity = (e) => {
    setCity_id(e.value)

  };


  return (
    <>
      {loading ? <Loading /> : null}
      <div className="main-calculator">
        <div className="header-container-pages">
          <Header choice="home" />
        </div>
        <section className="calculator">
          <div className="calculator-title-top">
            <h1>  </h1>
            <p>        </p>
          </div>
          <div className="calculator-container">
            <div className="box-menu-calculator">
              <div className="menu-calculator">
                <Link to={"/calculator"}>
                  <button className="item-menu-calculator active-btn">
                    <i class="fa-regular fa-calendar-days"></i>
                    <span> </span>
                  </button>
                </Link>
              </div>
              <div className="menu-calculator">
                <Link to={"/calculatorHome"}>
                  <button className="item-menu-calculator">
                    <i class="fa-solid fa-house-user"></i>
                    <span></span>
                  </button>
                </Link>
              </div>
              <div className="menu-calculator">
                <Link to={"/calculatorCar"}>
                  <button className="item-menu-calculator">
                    <i class="fa-solid fa-car"></i>
                    <span></span>
                  </button>
                </Link>
              </div>
              <div className="menu-calculator">
                <Link to={"/calculatorPublicTransport"}>
                  <button className="item-menu-calculator">
                    <i class="fa-solid fa-bus"></i>
                    <span>   </span>
                  </button>
                </Link>
              </div>
              <div className="menu-calculator">
                <Link to={"/calculatorMotorBike"}>
                  <button className="item-menu-calculator">
                    <i class="fa-solid fa-motorcycle"></i>
                    <span> </span>
                  </button>
                </Link>
              </div>
              <div className="menu-calculator">
                <button className="item-menu-calculator">
                  <i class="fa-sharp fa-solid fa-plane"></i>
                  <span></span>
                </button>
              </div>
              <div className="menu-calculator">
                <Link to={"/calculatorResult"}>
                  <button className="item-menu-calculator">
                    <i class="fa-solid fa-square-poll-vertical"></i>
                    <span></span>
                  </button>
                </Link>
              </div>
            </div>
            <div className="container-content-calculator">
              <div className="box-title-content-calculator">
                <p>
                          12  
                  
                </p>
                <p>
                              ():
                </p>
              </div>
              <div className="contetn-calculator">
                <div className="box-row-input-calculator">
                  <div className="row-form-input-calculator">
                    <label htmlFor="startTime-form"> : </label>
                    <div style={{ direction: "rtl" }}>
                      <DatePicker
                        calendar={persian}
                        locale={persian_fa}
                        calendarPosition="bottom-right"
                        onChange={(e) => {
                          setDataProperty("start_date", e.unix);
                        }}
                      />
                    </div>
                  </div>
                  <div className="row-form-input-calculator">
                    <label htmlFor="endTime-form"> : </label>
                    <div style={{ direction: "rtl" }}>
                      <DatePicker
                        calendar={persian}
                        locale={persian_fa}
                        calendarPosition="bottom-right"
                        onChange={(e) => {
                          setDataProperty("end_date", e.unix);
                        }}
                      />
                    </div>
                  </div>
                </div>
                <div className="box-row-input-calculator">
                  <div className="row-form-input-calculator">
                    <label htmlFor="ostan-input"> : </label>
                    <div className="box-input-search">
                      <Select
                        defaultValue={selectedStateOption}
                        onChange={handelerOptionCity} 
                        placeholder=" "
                        styles={{
                          control: (baseStyles, state) => ({
                            ...baseStyles,
                            // borderColor: state.isFocused ? '#067d68' : '#067d68',
                            width: "185px",
                            height: "36px",
                            borderRadius: "5px",
                            border: state.isFocused
                              ? "2px solid #067d68"
                              : "2px solid #067d68",

                            // This line disable the blue border
                            boxShadow: state.isFocused
                              ? "2px solid #067d68"
                              : "2px solid #067d68",
                            "&:hover": {
                              border: "2px solid #067d68",
                            },
                          }),
                        }}
                        theme={(theme) => ({
                          ...theme,
                          borderRadius: 0,
                          colors: {
                            ...theme.colors,
                            primary25: "#0ab373",
                            primary: "#143e35",
                          },
                        })}
                        options={optionsState}
                      />
                    </div>
                  </div>
                  <div className="row-form-input-calculator">
                    <label htmlFor="ostan-input"> : </label>
                    <div className="box-input-search">
                      <Select
                        defaultValue={selectedCityOption}
                        onChange={handelerOptionCityCity}
                        placeholder=" "
                        styles={{
                          control: (baseStyles, state) => ({
                            ...baseStyles,
                            // borderColor: state.isFocused ? '#067d68' : '#067d68',
                            width: "185px",
                            height: "36px",
                            borderRadius: "5px",
                            border: state.isFocused
                              ? "2px solid #067d68"
                              : "2px solid #067d68",

                            // This line disable the blue border
                            boxShadow: state.isFocused
                              ? "2px solid #067d68"
                              : "2px solid #067d68",
                            "&:hover": {
                              border: "2px solid #067d68",
                            },
                          }),
                        }}
                        theme={(theme) => ({
                          ...theme,
                          borderRadius: 0,
                          colors: {
                            ...theme.colors,
                            primary25: "#0ab373",
                            primary: "#143e35",
                          },
                        })}
                        options={optionsCity}
                      />
                    </div>
                  </div>
                </div>
              </div>
            </div>

            <button className="btn-calculator-next" onClick={onSubmit}>
              
            </button>
          </div>
        </section>
        <footer>
          <p>           .</p>
        </footer>
      </div>
      {/* )} */}
    </>
  );
};

export default Calculator;
