import React, { useEffect, useState } from "react";
import Header from "../../components/header";
import axios from "axios";
import { Link, useNavigate } from "react-router-dom";
import Loading from "../../components/loading";
import { toast } from "react-toastify";

const CalculatorHome = () => {
  const [loading, setLoading] = useState(false);

  const session_id = JSON.parse(localStorage.getItem("session_id"));
  const members = JSON.parse(localStorage.getItem("members"));
  const electricity = JSON.parse(localStorage.getItem("electricity"));
  const gas = JSON.parse(localStorage.getItem("gas"));
  const propane = JSON.parse(localStorage.getItem("propane"));
  const coal = JSON.parse(localStorage.getItem("coal"));
  const oil = JSON.parse(localStorage.getItem("oil"));
  const lpg = JSON.parse(localStorage.getItem("lpg"));

  const [textRes, setTextRes] = useState();
  let navigate = useNavigate();
  const [dataResult, setDataResult] = useState({});

  useEffect(() => {
    setDataResult({
      members: members,
      electricity: electricity,
      gas: gas,
      propane: propane,
      coal: coal,
      oil: oil,
      lpg: lpg,
    });
  }, []);

  const onSubmit = (e, validNavigate) => {
    e.preventDefault();

    setLoading(true);

    let membersResult = dataResult.members;
    let electricityResult = dataResult.electricity;
    let gasResult = dataResult.gas;
    let propaneResult = dataResult.propane;
    let coalResult = dataResult.coal;
    let oilResult = dataResult.oil;
    let lpgResult = dataResult.lpg;

    if (membersResult === undefined) {
      membersResult = members;
    }

    if (electricityResult === undefined) {
      electricityResult = electricity;
    }

    if (gasResult === undefined) {
      gasResult = gas;
    }

    if (propaneResult === undefined) {
      propaneResult = propane;
    }

    if (coalResult === undefined) {
      coalResult = coal;
    }

    if (oilResult === undefined) {
      oilResult = oil;
    }

    if (lpgResult === undefined) {
      lpgResult = lpg;
    }

    axios
      .post("http://146.70.87.250:8001/calculator/house", {
        session_id: parseInt(session_id),
        members: parseInt(membersResult),
        electricity: parseInt(electricityResult),
        gas: parseInt(gasResult),
        propane: parseInt(propaneResult),
        coal: parseInt(coalResult),
        oil: parseInt(oilResult),
        lpg: parseInt(lpgResult),
      })
      .then((res) => {
        if (validNavigate) {
          navigate("/calculatorCar");
        } else {
          setTextRes(
            <>
              <i class={`fas fa-cloud color-smoke-${res.data.color}`}></i>
              <p>  :
              &nbsp;
              <span className={`font-english color-smoke-${res.data.color}`}>
                 {res.data.house_overall} metric tons of CO2e 
              </span>
              
              </p>

              <div id="smoke">
                <span class="s0">
                  <i class={`fas fa-cloud color-smoke-${res.data.color}`}></i>
                </span>
                <span class="s1">
                  <i class={`fas fa-cloud color-smoke-${res.data.color}`}></i>
                </span>
                <span class="s2">
                  <i class={`fas fa-cloud color-smoke-${res.data.color}`}></i>
                </span>
                <span class="s3">
                  <i class={`fas fa-cloud color-smoke-${res.data.color}`}></i>
                </span>
                <span class="s4">
                  <i class={`fas fa-cloud color-smoke-${res.data.color}`}></i>
                </span>
                <span class="s5">
                  <i class={`fas fa-cloud color-smoke-${res.data.color}`}></i>
                </span>
                <span class="s6">
                  <i class={`fas fa-cloud color-smoke-${res.data.color}`}></i>
                </span>
                <span class="s7">
                  <i class={`fas fa-cloud color-smoke-${res.data.color}`}></i>
                </span>
                <span class="s8">
                  <i class={`fas fa-cloud color-smoke-${res.data.color}`}></i>
                </span>
                <span class="s9">
                  <i class={`fas fa-cloud color-smoke-${res.data.color}`}></i>
                </span>
              </div>
            </>
          );
        }
        localStorage.setItem("members", JSON.stringify(membersResult));
        localStorage.setItem("electricity", JSON.stringify(electricityResult));
        localStorage.setItem("gas", JSON.stringify(gasResult));
        localStorage.setItem("propane", JSON.stringify(propaneResult));
        localStorage.setItem("coal", JSON.stringify(coalResult));
        localStorage.setItem("oil", JSON.stringify(oilResult));
        localStorage.setItem("lpg", JSON.stringify(lpgResult));
        setLoading(false);
      })
      .catch((e) => {
        toast.error("    ");
        setLoading(false);
      });
  };
  const setDataProperty = (key, e) => {
    switch (key) {
      case "members":
        setDataResult({ ...dataResult, members: e.target.value });
        break;
      case "electricity":
        setDataResult({ ...dataResult, electricity: e.target.value });
        break;
      case "gas":
        setDataResult({ ...dataResult, gas: e.target.value });
        break;
      case "propane":
        setDataResult({ ...dataResult, propane: e.target.value });
        break;
      case "coal":
        setDataResult({ ...dataResult, coal: e.target.value });
        break;
      case "oil":
        setDataResult({ ...dataResult, oil: e.target.value });
        break;
      case "lpg":
        setDataResult({ ...dataResult, lpg: e.target.value });
        break;
    }
  };

  return (
    <>
      {loading ? <Loading /> : null}
      <div className="main-calculator">
        <div className="header-container-pages">
          <Header />
        </div>
        <section className="calculator">
          <div className="calculator-title-top">
            <h1>  </h1>
            <p>        </p>
          </div>
          <div className="calculator-container">
            <div className="box-menu-calculator">
              <div className="menu-calculator">
                <Link to={"/calculator"}>
                  <button className="item-menu-calculator">
                    <i class="fa-regular fa-calendar-days"></i>
                    <span> </span>
                  </button>
                </Link>
              </div>
              <div className="menu-calculator">
                <Link to={"/calculatorHome"}>
                  <button className="item-menu-calculator active-btn">
                    <i class="fa-solid fa-house-user"></i>
                    <span></span>
                  </button>
                </Link>
              </div>
              <div className="menu-calculator">
                <Link to={"/calculatorCar"}>
                  <button className="item-menu-calculator">
                    <i class="fa-solid fa-car"></i>
                    <span></span>
                  </button>
                </Link>
              </div>
              <div className="menu-calculator">
                <Link to={"/calculatorPublicTransport"}>
                  <button className="item-menu-calculator">
                    <i class="fa-solid fa-bus"></i>
                    <span>   </span>
                  </button>
                </Link>
              </div>
              <div className="menu-calculator">
                <Link to={"/calculatorMotorBike"}>
                  <button className="item-menu-calculator">
                    <i class="fa-solid fa-motorcycle"></i>
                    <span> </span>
                  </button>
                </Link>
              </div>
              <div className="menu-calculator">
                <button className="item-menu-calculator">
                  <i class="fa-sharp fa-solid fa-plane"></i>
                  <span></span>
                </button>
              </div>
              <div className="menu-calculator">
                <Link to={"/calculatorResult"}>
                  <button className="item-menu-calculator">
                    <i class="fa-solid fa-square-poll-vertical"></i>
                    <span></span>
                  </button>
                </Link>
              </div>
            </div>
            <div className="container-content-calculator">
              <div className="box-title-content-calculator">
                <p>
                              .
                </p>
              </div>
              <div className="contetn-calculator">
                <div className="align-item-calculator">
                  <div className="box-row-input-calculator">
                    <div className="row-form-input-calculator">
                      <label htmlFor="members-input">  : </label>
                      <div style={{ direction: "rtl" }}>
                        <input
                          type="number"
                          name="members"
                          placeholder="0"
                          id="members-input"
                          value={dataResult.members}
                          onChange={(e) => {
                            setDataProperty("members", e);
                          }}
                        />
                      </div>
                    </div>
                  </div>
                  <div className="box-row-input-calculator">
                    <div className="row-form-input-calculator">
                      <label htmlFor="electricity-input"> : </label>
                      <div style={{ direction: "rtl" }}>
                        <input
                          type="number"
                          name="electricity"
                          placeholder="kWh"
                          id="electricity-input"
                          value={dataResult.electricity}
                          onChange={(e) => {
                            setDataProperty("electricity", e);
                          }}
                        />
                      </div>
                    </div>
                  </div>
                  <div className="box-row-input-calculator">
                    <div className="row-form-input-calculator">
                      <label htmlFor="gas-input">  : </label>
                      <div style={{ direction: "rtl" }}>
                        <input
                          type="number"
                          name="gas"
                          placeholder=" "
                          id="gas-input"
                          value={dataResult.gas}
                          onChange={(e) => {
                            setDataProperty("gas", e);
                          }}
                        />
                      </div>
                    </div>
                  </div>
                  <div className="box-row-input-calculator">
                    <div className="row-form-input-calculator">
                      <label htmlFor="propane-input"> : </label>
                      <div style={{ direction: "rtl" }}>
                        <input
                          type="text"
                          name="propane"
                          placeholder=""
                          id="propane-input"
                          value={dataResult.propane}
                          onChange={(e) => {
                            setDataProperty("propane", e);
                          }}
                        />
                      </div>
                    </div>
                  </div>
                  <div className="box-row-input-calculator">
                    <div className="row-form-input-calculator">
                      <label htmlFor="coal-input">   : </label>
                      <div style={{ direction: "rtl" }}>
                        <input
                          type="number"
                          name="coal"
                          placeholder=" "
                          id="coal-input"
                          value={dataResult.coal}
                          onChange={(e) => {
                            setDataProperty("coal", e);
                          }}
                        />
                      </div>
                    </div>
                  </div>
                  <div className="box-row-input-calculator">
                    <div className="row-form-input-calculator">
                      <label htmlFor="oil-input">  : </label>
                      <div style={{ direction: "rtl" }}>
                        <input
                          type="number"
                          name="oil"
                          placeholder=""
                          id="oil-input"
                          value={dataResult.oil}
                          onChange={(e) => {
                            setDataProperty("oil", e);
                          }}
                        />
                      </div>
                    </div>
                  </div>
                  <div className="box-row-input-calculator">
                    <div className="row-form-input-calculator">
                      <label htmlFor="lpg-input">   (LPG) :</label>
                      <div style={{ direction: "rtl" }}>
                        <input
                          type="number"
                          name="lpg"
                          placeholder=" "
                          id="lpg-input"
                          value={dataResult.lpg}
                          onChange={(e) => {
                            setDataProperty("lpg", e);
                          }}
                        />
                      </div>
                    </div>
                  </div>
                  <button
                    className="btn-calculator-calculation"
                    onClick={(e) => {
                      onSubmit(e, false);
                    }}
                  >
                    
                  </button>
                  <div className="box-result-calculator">{textRes}</div>
                </div>
              </div>
            </div>
            <button
              className="btn-calculator-next"
              onClick={(e) => {
                onSubmit(e, true);
              }}
            >
              
            </button>
          </div>
        </section>
        <footer>
          <p>           .</p>
        </footer>
      </div>
    </>
  );
};

export default CalculatorHome;
