import React, { useEffect, useState } from "react";
import Header from "../../components/header";
import axios from "axios";
import { Link, useNavigate } from "react-router-dom";
import Loading from "../../components/loading";
import { toast } from 'react-toastify';

const CalculatorPublicTransport = () => {
  
  const [loading, setLoading] = useState(false);

  const session_id = JSON.parse(localStorage.getItem("session_id"));
  const bus = JSON.parse(localStorage.getItem("bus"));
  const train = JSON.parse(localStorage.getItem("train"));
  const metro = JSON.parse(localStorage.getItem("metro"));
  const taxi = JSON.parse(localStorage.getItem("taxi"));



  const [textRes, setTextRes] = useState();
  let navigate = useNavigate();
  const [dataResult, setDataResult] = useState({});

  
  useEffect(() => {
    setDataResult({
      bus: bus,
      train: train,
      metro: metro,
      taxi: taxi
    });
  }, []);

  const onSubmit = (e, validNavigate) => {

    e.preventDefault();

    setLoading(true)


    let busResult =  dataResult.bus;
    let trainResult = dataResult.train;
    let metroResult = dataResult.metro;
    let taxiResult = dataResult.taxi;


    
    if (busResult === undefined) {
      busResult = bus
    }

    if (trainResult === undefined) {
      trainResult = train
    }

    if (metroResult === undefined) {
      metroResult = metro
    }

    if (taxiResult === undefined) {
      taxiResult = taxi
    }

    axios
      .post("http://146.70.87.250:8001/calculator/public", {
        session_id: parseInt(session_id),
        bus: parseInt(busResult),
        train: parseInt(trainResult),
        metro: parseInt(metroResult),
        taxi: parseInt(taxiResult),
      })
      .then((res) => {

        if (validNavigate) {
          navigate("/calculatorMotorBike");
        } else {
          
          setTextRes(
            <>
              <i class={`fas fa-cloud color-smoke-${res.data.color}`}></i>
              <p>  :
              &nbsp;
              <span className={`font-english color-smoke-${res.data.color}`}>
                 {res.data.public_overall} metric tons of CO2e 
              </span>
              
              </p>

              <div id="smoke">
                <span class="s0">
                  <i class={`fas fa-cloud color-smoke-${res.data.color}`}></i>
                </span>
                <span class="s1">
                  <i class={`fas fa-cloud color-smoke-${res.data.color}`}></i>
                </span>
                <span class="s2">
                  <i class={`fas fa-cloud color-smoke-${res.data.color}`}></i>
                </span>
                <span class="s3">
                  <i class={`fas fa-cloud color-smoke-${res.data.color}`}></i>
                </span>
                <span class="s4">
                  <i class={`fas fa-cloud color-smoke-${res.data.color}`}></i>
                </span>
                <span class="s5">
                  <i class={`fas fa-cloud color-smoke-${res.data.color}`}></i>
                </span>
                <span class="s6">
                  <i class={`fas fa-cloud color-smoke-${res.data.color}`}></i>
                </span>
                <span class="s7">
                  <i class={`fas fa-cloud color-smoke-${res.data.color}`}></i>
                </span>
                <span class="s8">
                  <i class={`fas fa-cloud color-smoke-${res.data.color}`}></i>
                </span>
                <span class="s9">
                  <i class={`fas fa-cloud color-smoke-${res.data.color}`}></i>
                </span>
              </div>
            </>
          );
        }

    localStorage.setItem("bus", JSON.stringify(dataResult.bus));
    localStorage.setItem("train", JSON.stringify(dataResult.train));
    localStorage.setItem("metro", JSON.stringify(dataResult.metro));
    localStorage.setItem("taxi", JSON.stringify(dataResult.taxi));

        setLoading(false)


      })
      .catch((e) => {
        toast.error("    ");
        setLoading(false)
      });
  };
  const setDataProperty = (key, e) => {
    switch (key) {
      case "bus":
        setDataResult({ ...dataResult, bus: e.target.value });
        break;
      case "train":
        setDataResult({ ...dataResult, train: e.target.value });
        break;
      case "metro":
        setDataResult({ ...dataResult, metro: e.target.value });
        break;
      case "taxi":
        setDataResult({ ...dataResult, taxi: e.target.value });
        break;
    }
  };

  return (
    <>
    {loading ? (
      <Loading />
    ) : (null) }
    <div className="main-calculator">
      <div className="header-container-pages">
        <Header />
      </div>
      <section className="calculator">
        <div className="calculator-title-top">
          <h1>  </h1>
          <p>        </p>
        </div>
        <div className="calculator-container">
        <div className="box-menu-calculator">
            <div className="menu-calculator">
              <Link to={"/calculator"}>
                <button className="item-menu-calculator">
                  <i class="fa-regular fa-calendar-days"></i>
                  <span> </span>
                </button>
              </Link>
            </div>
            <div className="menu-calculator">
              <Link to={"/calculatorHome"}>
                <button className="item-menu-calculator">
                  <i class="fa-solid fa-house-user"></i>
                  <span></span>
                </button>
              </Link>
            </div>
            <div className="menu-calculator">
              <Link to={"/calculatorCar"}>
                <button className="item-menu-calculator">
                  <i class="fa-solid fa-car"></i>
                  <span></span>
                </button>
              </Link>
            </div>
            <div className="menu-calculator">
              <Link to={"/calculatorPublicTransport"}>
                <button className="item-menu-calculator active-btn">
                  <i class="fa-solid fa-bus"></i>
                  <span>   </span>
                </button>
              </Link>
            </div>
            <div className="menu-calculator">
              <Link to={"/calculatorMotorBike"}>
                <button className="item-menu-calculator">
                  <i class="fa-solid fa-motorcycle"></i>
                  <span> </span>
                </button>
              </Link>
            </div>
            <div className="menu-calculator">
              <button className="item-menu-calculator">
                <i class="fa-sharp fa-solid fa-plane"></i>
                <span></span>
              </button>
            </div>
            <div className="menu-calculator">
              <Link to={"/calculatorResult"}>
                <button className="item-menu-calculator">
                  <i class="fa-solid fa-square-poll-vertical"></i>
                  <span></span>
                </button>
              </Link>
            </div>
          </div>
          <div className="container-content-calculator">
            <div className="box-title-content-calculator">
              <p>      </p>
            </div>
            <div className="contetn-calculator">
              <div className="align-item-calculator">
                <div className="box-row-input-calculator">
                  <div className="row-form-input-calculator">
                    <label htmlFor="bus-input">  : </label>
                    <div style={{ direction: "rtl" }}>
                      <input
                        type="number"
                        name="bus"
                        placeholder="km"
                        id="bus-input"
                        value={dataResult.bus}
                        onChange={(e) => {
                          setDataProperty("bus", e);
                        }}
                      />
                    </div>
                  </div>
                </div>
                <div className="box-row-input-calculator">
                  <div className="row-form-input-calculator">
                    <label htmlFor="train-input"> : </label>
                    <div style={{ direction: "rtl" }}>
                      <input
                        type="number"
                        name="train"
                        placeholder="km"
                        id="train-input"
                        value={dataResult.train}
                        onChange={(e) => {
                          setDataProperty("train", e);
                        }}
                      />
                    </div>
                  </div>
                </div>
                <div className="box-row-input-calculator">
                  <div className="row-form-input-calculator">
                    <label htmlFor="taxi-input">  : </label>
                    <div style={{ direction: "rtl" }}>
                      <input
                        type="number"
                        name="taxi"
                        placeholder="km"
                        id="taxi-input"
                        value={dataResult.taxi}
                        onChange={(e) => {
                          setDataProperty("taxi", e);
                        }}
                      />
                    </div>
                  </div>
                </div>
                <div className="box-row-input-calculator">
                  <div className="row-form-input-calculator">
                    <label htmlFor="subway-input">  : </label>
                    <div style={{ direction: "rtl" }}>
                      <input
                        type="number"
                        name="subway"
                        placeholder="km"
                        id="subway-input"
                        value={dataResult.metro}
                        onChange={(e) => {
                          setDataProperty("metro", e);
                        }}
                      />
                    </div>
                  </div>
                </div>

                <button className="btn-calculator-calculation"                  onClick={(e) => {
                    onSubmit(e, false);
                  }}></button>
                <div className="box-result-calculator">
                {textRes}
                </div>
              </div>
            </div>
          </div>
          <button className="btn-calculator-next"             onClick={(e) => {
              onSubmit(e, true);
            }}></button>
        </div>
      </section>
      <footer>
        <p>
                   .
        </p>
      </footer>
    </div>
    </>

  );
};

export default CalculatorPublicTransport;
