import React, { useState } from "react";
import { Link } from "react-router-dom";
import Header from "../../components/header";
import reduce_img from "../../asset/image/reduce_img.png";
import reduceImfg2 from "../../asset/image/reduceImfg2.png";
import { toast } from "react-toastify";
import axios from "axios";

const Reduce = () => {
    const [dataResult, setDataResult] = useState({});
    const [textRes, setTextRes] = useState();
  
    function separate(Number) {
      Number += "";
      Number = Number.replace(",", "");
      let x = Number.split(".");
      let y = x[0];
      let z = x.length > 1 ? "." + x[1] : "";
      var rgx = /(\d+)(\d{3})/;
      while (rgx.test(y)) y = y.replace(rgx, "$1" + "," + "$2");
      return y + z;
    }
  
    const handeler = (e) => {
      e.preventDefault();
  
    //   setLoading(true);
  
      axios
        .post("http://146.70.87.250:8001/calculator/only_price", {
          carbon_metric_tone: parseFloat(dataResult.efficiency),
        })
        .then((res) => {
          setTextRes(
  
              <>
              <i class={`fas fa-cloud color-smoke-${res.data.color === "green" ? "green-dark" : res.data.color}`}></i>
              <p> : </p>
              &nbsp;
              <p className={`color-smoke-${res.data.color === "green" ? "green-dark" : res.data.color}`}>{separate(res.data.carbon_price)} </p>
  
                {/* <p>  :
                &nbsp;
                <span className={`font-english color-smoke-${res.data.color === "green" ? "green-dark" : res.data.color}`}>
                   {res.data.carbon_metric_tone} metric tons of CO2e 
                </span>
                
                </p> */}
  
                <div id="smoke">
                  <span class="s0">
                    <i class={`fas fa-cloud color-smoke-${res.data.color === "green" ? "green-dark" : res.data.color}`}></i>
                  </span>
                  <span class="s1">
                    <i class={`fas fa-cloud color-smoke-${res.data.color === "green" ? "green-dark" : res.data.color}`}></i>
                  </span>
                  <span class="s2">
                    <i class={`fas fa-cloud color-smoke-${res.data.color === "green" ? "green-dark" : res.data.color}`}></i>
                  </span>
                  <span class="s3">
                    <i class={`fas fa-cloud color-smoke-${res.data.color === "green" ? "green-dark" : res.data.color}`}></i>
                  </span>
                  <span class="s4">
                    <i class={`fas fa-cloud color-smoke-${res.data.color === "green" ? "green-dark" : res.data.color}`}></i>
                  </span>
                  <span class="s5">
                    <i class={`fas fa-cloud color-smoke-${res.data.color === "green" ? "green-dark" : res.data.color}`}></i>
                  </span>
                  <span class="s6">
                    <i class={`fas fa-cloud color-smoke-${res.data.color === "green" ? "green-dark" : res.data.color}`}></i>
                  </span>
                  <span class="s7">
                    <i class={`fas fa-cloud color-smoke-${res.data.color === "green" ? "green-dark" : res.data.color}`}></i>
                  </span>
                  <span class="s8">
                    <i class={`fas fa-cloud color-smoke-${res.data.color === "green" ? "green-dark" : res.data.color}`}></i>
                  </span>
                  <span class="s9">
                    <i class={`fas fa-cloud color-smoke-${res.data.color === "green" ? "green-dark" : res.data.color}`}></i>
                  </span>
                </div>
              </>
              );
        //   setLoading(false);
        })
        .catch((e) => {
          toast.error("    ");
        //   setLoading(false);
        });
    };
  
    const setDataProperty = (key, e) => {
      switch (key) {
        case "efficiency":
          setDataResult({ ...dataResult, efficiency: e.target.value });
          break;
      }
    };
  
    
  return (
    <div className="main-information">
      <div className="header-container-pages">
        <Header />
      </div>
      <section className="information">
        <h1 className="title-information">REDUCE</h1>
        <p className="title-p-information" style={{textAlign:"center"}}>
        Helping You To Reduce Your Carbon Footprint
</p>
        <img src={reduce_img} alt="img" />

        <h3 className="title-p-information">
        Reducing emissions is the key solution to tackling climate change   </h3>
      


      <p>
      Whether you are an individual looking for some tips and advice, or a business -  we can help you. Please select from the two options below:


      </p>

      <img src={reduceImfg2} alt="img" />   
        <hr style={{width: "100%"}}/> 
               <h3 className="title-p-information">
               Don't forget you can also help support carbon reduction projects in other parts of the world   </h3>
      
        <p>
        It is very difficult to reduce emissions to zero, so think about Offsetting the emissions you can't reduce any further or emissions you have already caused.
</p>
        <section className="calculator-home">
          <div className="container-calculator-home">
            <h3>
                   
              <span className="green-bold-text">  </span>
                   
            </h3>
            <div className="box-calculator-home">
              <div className="box-input-p-calculator-home">
                <label for="inptu-calculator-home">
                  {" "}
                   <span className="font-english">tCO2</span>
                </label>
                <input
                  id="inptu-calculator-home"
                  className="inptu-calculator-home"
                  placeholder=" : 2/3"
                  type="number"
                  onChange={(e) => {
                    setDataProperty("efficiency", e);
                  }}
                />
                <div className="box-result-calculator">{textRes}</div>
              </div>
              <button className="btn-calculator-home" onClick={handeler}>
                 
              </button>
            </div>
          </div>
        </section>
      
      
      </section>
      <footer>
        <p>           .</p>
      </footer>
    </div>
  );
};

export default Reduce;
